import {Component, OnInit} from '@angular/core';
//import {globals} from '../../globals/global.constats';
import {APIService} from '../../globals/services/API.service';
import {hunkDate} from '../../globals/pipes/hunkDate.pipe';
import {MyInnerHtmlComponent} from '../../components/my-inner-html/my-inner-html.component';

@Component({
    selector: 'eredmeny',
    moduleId: module.id,
    templateUrl: 'eredmeny.html',
    providers: [APIService],
    styleUrls: [""],
    directives: [MyInnerHtmlComponent],
    pipes:[hunkDate]
})
export class EredmenyComponent implements OnInit {

    eredmenyek:any=[];
    constructor(private _APIService:APIService) {

    }

    ngOnInit() {
        this.loadEredmenyek();
    }

    loadEredmenyek() {
        this._APIService.getResponseGET('api/eredmenyeks/where=id>0&orderBy=datum&asc=false', '').subscribe(
                data=> {
                    console.log(data);
                    this.eredmenyek=data;

            },
                error=> {
                console.log(error);
            })

    }
}
